"use client"

import { useEffect, useRef } from "react"

export default function CircleSection() {
  const innerTextRef = useRef<HTMLDivElement>(null)
  const outerTextRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const handleScroll = () => {
      const y = window.scrollY

      if (innerTextRef.current) {
        innerTextRef.current.style.transform =
          `translate(-50%, -50%) rotate(${y * 0.12}deg)`
      }
      if (outerTextRef.current) {
        outerTextRef.current.style.transform =
          `translate(-50%, -50%) rotate(${y * -0.06}deg)`
      }
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <section className="relative w-full min-h-screen bg-[#0E0E0E] overflow-hidden flex items-center justify-center py-28">

{/* LEFT IMAGE – slightly higher */}
<div className="absolute left-[2%] top-[18%] hidden lg:block">
  <div className="relative w-[260px] h-[260px] rounded-full border border-white/10 flex items-center justify-center">
    <img
      src="/circle_l-500x500.jpg"
      className="w-[210px] h-[210px] rounded-full object-cover"
    />
  </div>
</div>

{/* RIGHT IMAGE – slightly lower */}
<div className="absolute right-[2%] top-[58%] hidden lg:block">
  <div className="relative w-[260px] h-[260px] rounded-full border border-white/10 flex items-center justify-center">
    <img
      src="/circle_r.jpg"
      className="w-[210px] h-[210px] rounded-full object-cover"
    />
  </div>
</div>


      {/* CENTER WRAPPER */}
      <div className="relative w-[880px] h-[880px] flex items-center justify-center">

        {/* CENTER IMAGE */}
        <div className="absolute w-[380px] h-[380px] rounded-full overflow-hidden z-[5]">
          <img src="/circle_img.jpg" className="w-full h-full object-cover" />
        </div>

{/* INNER TEXT RING */}
<div
  ref={innerTextRef}
  className="absolute top-1/2 left-1/2 z-[4]"
  style={{ transform: "translate(-50%, -50%)" }}
>
  <svg className="w-[760px] h-[760px]" viewBox="0 0 760 760">
    <defs>
      <path
        id="innerCircle"
        d="M 380 130 A 250 250 0 1 1 379.9 130"
      />
    </defs>

    <text fontSize="72" fill="#E7D9CD" fontWeight="700" letterSpacing="4">
      <textPath href="#innerCircle">
        HAIRSTYLE MASTERS — HAIRSTYLE MASTERS —
      </textPath>
    </text>
  </svg>
</div>



{/* OUTER TEXT RING */}
<div
  ref={outerTextRef}
  className="absolute top-1/2 left-1/2 opacity-[0.35]"
  style={{ transform: "translate(-50%, -50%)" }}
>
  <svg className="w-[820px] h-[820px]" viewBox="0 0 820 820">
    <defs>
      <path
        id="outerCircle"
        d="M 410 80 A 330 330 0 1 1 409.9 80"
      />
    </defs>

    <text fontSize="42" fill="#ffffff" fontWeight="600" letterSpacing="3">
      <textPath href="#outerCircle">
        WE COMBINE ARTISTRY, MODERN INNOVATION, AND TRADITIONAL SKILLS TO DELIVER
        LUXURY HAIRCARE, BRIDAL STYLING, AND PERSONALISED BEAUTY SERVICES FOR EVERY CLIENT —
      </textPath>
    </text>
  </svg>
</div>





      </div>
    </section>
  )
}
